/* 毛玻璃动态效果样式 */

/* CSS变量 - 主题颜色 */
:root {
  --bg-gradient-start: #5b5fc7;
  --bg-gradient-end: #3a8bd8;
  --bubble-color-1: #ff9966;
  --bubble-color-2: #4da6ff;
  --text-color: #ffffff;
  --text-color-secondary: rgba(255, 255, 255, 0.7);
  --border-color: rgba(255, 255, 255, 0.2);
  --glass-bg-color: rgba(255, 255, 255, 0.15);
  --glass-bg-hover: rgba(255, 255, 255, 0.25);
  --glass-border: rgba(255, 255, 255, 0.2);
  --glass-border-hover: rgba(255, 255, 255, 0.3);
  --shadow-color: rgba(0, 0, 0, 0.1);
  --shadow-color-hover: rgba(0, 0, 0, 0.15);
}

/* 白天模式变量 */
[data-theme="light"] {
  --bg-gradient-start: #e0e5ff;
  --bg-gradient-end: #c5d8ff;
  --bubble-color-1: #ffb380;
  --bubble-color-2: #80b3ff;
  --text-color: #333333;
  --text-color-secondary: rgba(0, 0, 0, 0.7);
  --border-color: rgba(0, 0, 0, 0.1);
  --glass-bg-color: rgba(255, 255, 255, 0.7);
  --glass-bg-hover: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(0, 0, 0, 0.1);
  --glass-border-hover: rgba(0, 0, 0, 0.15);
  --shadow-color: rgba(0, 0, 0, 0.05);
  --shadow-color-hover: rgba(0, 0, 0, 0.1);
}

/* 动态背景 */
.dynamic-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  background: linear-gradient(135deg, var(--bg-gradient-start) 0%, var(--bg-gradient-end) 100%);
  transition: background 0.5s ease;
}

.dynamic-bg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.02) 100%);
  opacity: 0.5;
}

.dynamic-bg::after {
  content: none; /* 移除额外的覆盖层，让背景更清晰 */
}

/* 动态气泡 */
.bubble {
  position: absolute;
  border-radius: 50%;
  filter: blur(30px);
  z-index: -1;
  transition: background-color 0.5s ease;
}

.bubble:nth-child(1) {
  width: 400px;
  height: 400px;
  bottom: -100px;
  left: 10%;
  background: var(--bubble-color-1);
  opacity: 0.5;
  animation: float 25s ease-in-out infinite;
}

.bubble:nth-child(2) {
  width: 350px;
  height: 350px;
  top: -50px;
  right: 10%;
  background: var(--bubble-color-2);
  opacity: 0.5;
  animation: float 20s ease-in-out infinite;
  animation-delay: 2s;
}

.bubble:nth-child(3) {
  display: none;
}

.bubble:nth-child(4) {
  display: none;
}

.bubble:nth-child(5) {
  display: none;
}

/* 毛玻璃效果 */
.glassmorphism {
  background: var(--glass-bg-color) !important;
  backdrop-filter: blur(15px) !important;
  -webkit-backdrop-filter: blur(15px) !important;
  border: 1px solid var(--glass-border) !important;
  box-shadow: 0 8px 32px var(--shadow-color) !important;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1) !important;
  border-radius: 16px !important;
}

.glassmorphism:hover {
  background: var(--glass-bg-hover) !important;
  box-shadow: 0 10px 40px var(--shadow-color-hover) !important;
  transform: translateY(-2px) !important;
  border: 1px solid var(--glass-border-hover) !important;
}

.glassmorphism-button {
  background: var(--glass-bg-color) !important;
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border: 1px solid var(--glass-border) !important;
  box-shadow: 0 4px 12px var(--shadow-color) !important;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1) !important;
  border-radius: 12px !important;
  color: var(--text-color) !important;
}

.glassmorphism-button:hover {
  background: var(--glass-bg-hover) !important;
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 16px var(--shadow-color-hover) !important;
  border: 1px solid var(--glass-border-hover) !important;
}

/* 动画 */
@keyframes rotateBg {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes float {
  0% {
    transform: translateY(0) translateX(0);
    opacity: 0.4;
  }
  25% {
    transform: translateY(-20px) translateX(10px);
    opacity: 0.5;
  }
  50% {
    transform: translateY(0) translateX(20px);
    opacity: 0.4;
  }
  75% {
    transform: translateY(20px) translateX(10px);
    opacity: 0.5;
  }
  100% {
    transform: translateY(0) translateX(0);
    opacity: 0.4;
  }
}

/* 响应式调整 */
@media (max-width: 768px) {
  .bubble {
    opacity: 0.3;
  }
}

/* 主题切换过渡效果 */
.dynamic-bg, .glassmorphism, .glassmorphism-button, .bubble {
  transition: all 0.5s ease;
} 